import { Component } from "react";

export default class Loader extends Component {
  render() {
    return (
      <>
        <p style={{ textAlign: 'center' }}>
          {this.props.text.split('|').map(function(item, key) {
            return (
              <span key={key}>
                {item}<br/>
              </span>
            );
          })}
        </p>
        <svg version="1.1" id="L4" xmlns="http://www.w3.org/2000/svg" x="0px" y="0px" width="100px" height="50px" 
          viewBox="0 0 100 100" enableBackground="new 0 0 0 0" xmlSpace="preserve">
          <circle fill="#ccc" stroke="none" cx="30" cy="10" r="6">
            <animate
              attributeName="opacity"
              dur="1s"
              values="0;1;0"
              repeatCount="indefinite"
              begin="0.1"/>    
          </circle>
          <circle fill="#ccc" stroke="none" cx="50" cy="10" r="6">
            <animate
              attributeName="opacity"
              dur="1s"
              values="0;1;0"
              repeatCount="indefinite" 
              begin="0.2"/>       
          </circle>
          <circle fill="#ccc" stroke="none" cx="70" cy="10" r="6">
            <animate
              attributeName="opacity"
              dur="1s"
              values="0;1;0"
              repeatCount="indefinite" 
              begin="0.3"/>     
          </circle>
        </svg>
      </>
    );
  }
}